 *,
 *::before,
 *::after {
     box-sizing: border-box;
 }

 :root {
      --ff-primary: 'Source Sans Pro', sans-serif;
      --ff-secondary: 'Source Sans Pro', monospace;

      --fw-reg: 300;
      --fw-bold: 900;

      --clr-light: #fff;
      --clr-dark: #303030;
      --clr-accent: #16e0bd;

      --fs-h1: 3rem;
      --f2-h2: 2.25rem;
      --fs-h3: 1.25rem;
      --fs-body: 1rem;

      --bs: 0.25em 0.25em 0.75em rgba(0,0,0,.25),
      0.125em 0.125em 0.25em rgba(0,0,0,.15);
 }

 @media (min-width: 800px) {
     :root {
         --fs-h1: 4.5rem;
         --fs-h2: 3.75rem;
         --fs-h3: 1.5rem;
         --fs-body: 1.125rem;
     }
 }

 /* General Styles */

 .wrapper {
     background: #fff;
     background: var(--clr-light);
     color: #303030;
     color: var(--clr-dark);
     margin: 0;
     font-family: 'Source Sans Pro', sans-serif;
     font-family: var(--ff-primary);
     font-size: 1rem;
     font-size: var(--fs-body);
     line-height: 1.6em;
 }

 section {
     padding: 5em 2em;
 }

 img {
     display: block;
     max-width: 100%;
 }

 strong { font-weight: 900; font-weight: var(--fw-bold); }

 :focus {
     outline: 3px solid #16e0bd;
     outline: 3px solid var(--clr-accent);
     outline-offset: 3px;
 }

 /*  Button */

 .btn {
     display: inline-block;
     padding: .5em 2.5em;
     background: #16e0bd;
     background: var(--clr-accent);
     color: #303030;
     color: var(--clr-dark);
     text-decoration: none;
     cursor: pointer;
     font-size: .8rem;
     text-transform: uppercase;
     letter-spacing: 2px;
     font-weight: 900;
     font-weight: var(--fw-bold);
     -webkit-transition: -webkit-transform 200ms ease-in-out;
     transition: -webkit-transform 200ms ease-in-out;
     transition: transform 200ms ease-in-out;
     transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
 }

 .btn:hover {
     -webkit-transform: scale(1.1);
             transform: scale(1.1);
 }

/* Typography */

h1,
h2,
h3 {
    line-height: 1.1;
    margin: 0;
}

h1 { font-size: 3rem; font-size: var(--fs-h1);}
h2 { font-size: var(--fs-h2);}
h3 { font-size: 1.25rem; font-size: var(--fs-h3);  font-weight: 900;  font-weight: var(--fw-bold);}

.section__title {
    margin-bottom: .25em;
}

.section__title--intro {
    font-weight: 300;
    font-weight: var(--fw-reg);
}

.section__title--intro strong {
    display: block;
}

.section__subtitle {
    margin: 0;
    font-size: 1.25rem;
    font-size: var(--fs-h3);
}

.section__subtitle--intro,
.section__subtitle--about {
    background: #16e0bd;
    background: var(--clr-accent);
    padding: .25em 1em;
    font-family: 'Source Sans Pro', monospace;
    font-family: var(--ff-secondary);
    margin-bottom: 1em;
}

/*  header  */

header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 1em;
}

.logo { 
    max-width: 100px;
}

.nav {
    position: fixed;
    background: #303030;
    background: var(--clr-dark);
    color: #fff;
    color: var(--clr-light);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;

    -webkit-transform: translateX(100%);

            transform: translateX(100%);
    -webkit-transition: -webkit-transform 250ms cubic-bezier(.5,0,.5,1);
    transition: -webkit-transform 250ms cubic-bezier(.5,0,.5,1);
    transition: transform 250ms cubic-bezier(.5,0,.5,1);
    transition: transform 250ms cubic-bezier(.5,0,.5,1), -webkit-transform 250ms cubic-bezier(.5,0,.5,1);
}

.nav__list {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    margin: 0;
    padding: 0;
    width: 100%;
}

.nav__link {
    color: inherit;
    font-weight: 900;
    font-weight: var(--fw-bold);
    font-size: 2.25rem;
    font-size: var(--f2-h2);
    text-decoration: none;
}

.nav__link:hover {
    color: #16e0bd;
    color: var(--clr-accent);
}

.nav-toggle {
    padding: .5em;
    background: transparent;
    border: 0;
    cursor: pointer;
    position: absolute;
    right: 1em;
    top: 1em;
    z-index: 1000;
}

.nav-open .nav{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.nav-open .nav-toggle {
    position: fixed;
}

.nav-open .hamburger {
    -webkit-transform: rotate(.625turn);
            transform: rotate(.625turn);
}

.nav-open .hamburger:before {
    -webkit-transform: rotate(90deg) translateX(-6px);
            transform: rotate(90deg) translateX(-6px);
}

.nav-open .hamburger:after {
    opacity: 0;
}

.hamburger {
    display: block;
    position: relative;
}

.hamburger,
.hamburger:before,
.hamburger:after {
    background: #16e0bd;
    background: var(--clr-accent);
    width: 2em;
    height: 3px;
    border-radius: 1em;
    -webkit-transition: -webkit-transform 250ms ease-in-out;
    transition: -webkit-transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}

.hamburger:before,
.hamburger:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
}

.hamburger:before {
    top: 6px;
}

.hamburger:after { bottom: 6px; }


/*  Intro Section   */

.intro {
    position: relative;
}

.intro__img {
    box-shadow: 0.25em 0.25em 0.75em rgba(0,0,0,.25),
      0.125em 0.125em 0.25em rgba(0,0,0,.15);
    box-shadow: var(--bs);
}

@media (min-width: 600px) {
    .intro {
        display: grid;
        width: -webkit-min-content;
        width: -moz-min-content;
        width: min-content;
        margin: 0 auto;
        grid-column-gap: 1em;
        grid-template-areas: 
            "img title"
            "img subtitle";
        grid-template-columns: -webkit-min-content -webkit-max-content;
        grid-template-columns: min-content max-content;
        margin-bottom: 5em;
    }

    .intro__img {
        grid-area: img;
        min-width: 250px;
        position: relative;
        z-index: 2;
    }

    .section__subtitle--intro {
        align-self: start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
    }
}

/* My services section */

.my-services {
