}

.section__title--services {
    color: #16e0bd;
    color: var(--clr-accent);
    position: relative;
}

.section__title--services::after {
    content: '';
    display: block;
    width: 2em;
    height: 1px;
    margin: 0.5em auto 1em;
    background: #fff;
    background: var(--clr-light);
    opacity: 0.25;
}

.services {
    margin-bottom: 4em;
}

.service {
    max-width: 500px;
    margin: 0 auto;
}

@media(min-width: 800px) {
    .services {
        display: -webkit-flex;
        display: flex;
        max-width: 1000px;
        margin: 0 auto;
        margin-bottom: 4em;
    }

    /* .service + .service {
        margin-left: 2em;
    } */
}

/*      About Me     */

.about-me {
    max-width: 1000px;
    margin: 0 auto;
}

.about-me__img {
    box-shadow: 0.25em 0.25em 0.75em rgba(0,0,0,.25),
      0.125em 0.125em 0.25em rgba(0,0,0,.15);
    box-shadow: var(--bs);
}

@media (min-width: 600px) {
    .about-me {
        display: grid;
        grid-template-columns: 1fr 200px;
        grid-template-areas:
            "title img"
            "subtitle img"
            "text img";
        grid-column-gap: 2em;
    }
    
    .section__title--about {
        grid-area: title;
    }

    .section__subtitle--about {
        grid-column: 1 / -1;
        grid-row: 2;
        position: relative;
        left: -1em;
        width: calc(100% + 2em);
        padding-left: 1em;
        padding-right: calc(200px + 2em);
    }

    .about-me__img {
        grid-area: img;
        position: relative;
        z-index: 2;
    }
}

/* My Work */

.my-work {
    background-color: #303030;
    background-color: var(--clr-dark);
    color: #fff;
    color: var(--clr-light);
    text-align: center;
}

.section__subtitle--work {
    color: #16e0bd;
    color: var(--clr-accent);
    font-weight: 900;
    font-weight: var(--fw-bold);
    margin-bottom: 2em;
}

.portfolio {
    display: grid;
    /* grid-template-columns: repeat(3, 1fr); */
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.portfolio__item {
    background: #16e0bd;
    background: var(--clr-accent);
    overflow: hidden;
}

.portfolio__img {
    -webkit-transition: 
        -webkit-transform 750ms cubic-bezier(.5,0,.5,1)
        opacity 250ms linear;
    transition: 
        -webkit-transform 750ms cubic-bezier(.5,0,.5,1)
        opacity 250ms linear;
    transition: 
        transform 750ms cubic-bezier(.5,0,.5,1)
        opacity 250ms linear;
    transition: 
        transform 750ms cubic-bezier(.5,0,.5,1)
        opacity 250ms linear, -webkit-transform 750ms cubic-bezier(.5,0,.5,1)
        opacity 250ms linear;
}

.portfolio__item:focus {
    position: relative;
    z-index: 2;
}

.portfolio__img:hover,
.portfolio__item:focus .portfolio__img {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: .5;
}

